import { NextResponse } from 'next/server'
import { prisma } from '@/lib/db'

export const dynamic = 'force-dynamic'

// GET /api/debug/auth-check - Check auth configuration (temporary debug endpoint)
export async function GET() {
  const checks: Record<string, any> = {
    timestamp: new Date().toISOString(),
    environment: process.env.NODE_ENV,
  }

  // Check environment variables (don't expose values, just existence)
  checks.env = {
    DATABASE_URL: !!process.env.DATABASE_URL,
    DATABASE_URL_has_pgbouncer: process.env.DATABASE_URL?.includes('pgbouncer=true') || false,
    DIRECT_URL: !!process.env.DIRECT_URL,
    NEXTAUTH_SECRET: !!process.env.NEXTAUTH_SECRET,
    NEXTAUTH_URL: process.env.NEXTAUTH_URL || 'NOT SET',
  }

  // Check Prisma connection
  checks.prisma = {
    clientExists: !!prisma,
  }

  if (prisma) {
    try {
      // Try a simple query
      const userCount = await prisma.user.count()
      checks.prisma.connected = true
      checks.prisma.userCount = userCount

      // Check if admin exists
      const admin = await prisma.user.findUnique({
        where: { email: 'admin@risivo.com' },
        select: { 
          id: true, 
          email: true, 
          status: true, 
          role: true,
          isMasterAdmin: true,
          password: true 
        }
      })

      if (admin) {
        checks.admin = {
          exists: true,
          id: admin.id,
          email: admin.email,
          status: admin.status,
          role: admin.role,
          isMasterAdmin: admin.isMasterAdmin,
          hasPassword: !!admin.password,
          passwordLength: admin.password?.length || 0,
        }
      } else {
        checks.admin = { exists: false }
      }
    } catch (error: any) {
      checks.prisma.connected = false
      checks.prisma.error = error.message
    }
  }

  return NextResponse.json(checks)
}
